import { defineComponent } from "vue";
import { mapActions } from "vuex";
import Loading from "vue3-loading-overlay";
import BotCardPlaceHolder from "@/components/PlaceHolders/BotCardPlaceHolder/index.vue";
import BotoModal from "@/components/BotoModal/index.vue";
import DrawFlow from "@/components/DrawFlow/index.vue";
import EventEmitter from "@/util/EventEmitter";
import { HttpRequestMethod } from "@/enums/HttpRequestMethod";
import HttpClient from "@/util/HttpClient";

import "vue3-loading-overlay/dist/vue3-loading-overlay.css";
import "vue-toast-notification/dist/theme-sugar.css";
import { Boto } from "@/interfaces/Boto";

export default defineComponent({
  name: "Home",
  components: {
    BotCardPlaceHolder,
    BotoModal,
    DrawFlow,
    Loading,
  },
  setup() {
    const min = Math.ceil(1);
    const max = Math.floor(2);
    const image = `i_${Math.floor(Math.random() * (max - min + 1)) + min}.gif`;
    return {
      image,
    };
  },
  data() {
    return {
      context: process.env.VUE_APP_CONTEXT,
      isTemplateLoading: false,
      isLoadingData: true,
      isLoadingTemplateData: false,
      blocks: null,
      fullPage: true,
      template: null as any,
      templateId: "",
      showTemplate: false,
      isLoading: false,
      isFlowValid: false,
    };
  },
  async created() {
    if (!navigator.onLine) {
      this.$router.push({
        name: "home.offline",
        params: { route: this.$route.fullPath },
      });
    }

    setTimeout(() => (this.isLoadingData = false), 1000);
  },
  async mounted() {
    try {
      this.templateId = this.$route.params.template?.toString();
      if (this.templateId) {
        const template = await this.getTemplate(this.templateId);
        this.storeBotTemplate(template, true);
      }

      if (this.$auth.isAuthenticated.value) {
        if (!this.templateId)
          this.$router.push({ name: "home.dashboard", params: {} });
        else if (this.templateId) {
          this.$router.push({
            name: "home.editor",
            query: { templateId: this.templateId, type: "recipe" },
          });
        }
      } else {
        const blocks = await this.getBlocks();
        this.blocks = blocks.filter(function (el: any) {
          return el.published === true;
        });
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const vm = this;
        EventEmitter.emitter.on(
          "editor-valid-status",
          function (status: boolean) {
            vm.isFlowValid = status;
          }
        );

        if (this.templateId)
          this.showEditor(
            this.templateId,
            this.getTemplateHelpText(this.templateId)
          );
      }
    } catch (e) {
      this.$toast.open({
        message: "There was a problem.",
        type: "error",
        duration: 5000,
        dismissible: true,
        position: "top-right",
      });
    }
  },
  methods: {
    ...mapActions("editor", ["setTemplate", "setTemplateLocalStorage"]),
    getTemplateHelpText(templateId: string) {
      let helpText = "";
      switch (templateId.toString()) {
        case "413":
          helpText =
            "This recipe will send to Discord information about Minted NFTs. Checkout <a href='https://medium.com/boto-corp/opensea-nfts-to-discord-bot-no-code-eeec8340112d'>our post to get the Channel ID</a>";
          break;
        case "324":
          helpText =
            "This recipe will send information about NFTs sales. Checkout <a href='https://medium.com/boto-corp/opensea-nfts-to-discord-bot-no-code-eeec8340112d'>our post to get the Channel ID</a>";
          break;
        case "497":
          helpText =
            "This recipe will monitor via Telegram a Whale's Portfolio.";
          break;
        case "4942":
          helpText =
            "This recipe will send email notifications periodically with your ETH balance.";
          break;
        default:
          break;
      }
      return helpText;
    },
    openTemplate(templateId: string) {
      this.$router.push({
        name: "home.template",
        params: { template: templateId },
      });
      this.showEditor(templateId, this.getTemplateHelpText(templateId));
    },
    async showEditor(templateId: string, helpText: string) {
      try {
        this.isLoadingTemplateData = true;
        if (templateId) {
          const template = await this.getTemplate(templateId);
          setTimeout(() => {
            const editor = (this.$refs.drawflowEditor as any)?.editor;
            const html = `
          <div class="info-box"><div class="title-box"><i class="fas fa-info-circle"></i> <strong>Welcome!</strong></div><div class="box"><p>${helpText} <br> You can use it straight away by filling in your details and clicking "Save Bot" ðŸ˜‹</p></div></div>
          `;
            const data = { name: "" };
            editor.addNode(
              "templateHelp",
              0,
              0,
              editor.precanvas.clientWidth - 410,
              20,
              "help",
              data,
              html
            );
          }, 200);
          this.template = template;
          this.showTemplate = true;
          this.isLoadingTemplateData = false;
        }
      } catch (e) {
        this.$toast.open({
          message: "There was a problem trying to load this template.",
          type: "error",
          duration: 5000,
          dismissible: true,
          position: "top-right",
        });
        this.isLoadingTemplateData = false;
      }
    },
    handleCancel() {
      this.$router.push({ name: "home" });
      this.showTemplate = false;
    },
    handleConfirm() {
      this.storeBotTemplate(this.template);
      this.login();
    },
    storeBotTemplate(template: Boto, skipEditor = false) {
      this.isTemplateLoading = true;
      let json = null;
      if (!skipEditor) {
        // @ts-expect-error ignore
        json = this.$refs.drawflowEditor?.export();
        // Remove help box
        for (const [key, value] of Object.entries(json.json.data)) {
          if ((value as any).name === "templateHelp")
            delete json.json.data[key];
        }
        delete json.json.data[json.json.data.length];
      }
      let templateToStore = {
        ...template,
        templateId: template.id,
        clone_of: template.id,
      };
      if (json) templateToStore = { ...templateToStore, ...json };
      // why do we delete id?
      delete templateToStore.id;
      this.setTemplate(templateToStore);
      this.setTemplateLocalStorage(templateToStore);
      this.isTemplateLoading = false;
    },
    async getBlocks() {
      return await HttpClient.request(
        `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/blocks/`,
        null,
        HttpRequestMethod.GET
      );
    },
    async getTemplate(templateId: string) {
      return await HttpClient.request(
        `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bot_template/${templateId}/`,
        null,
        HttpRequestMethod.GET
      );
    },
    login() {
      this.$auth.loginWithRedirect();
    },
    logout() {
      this.$auth.logout({
        returnTo: `${window.location.origin}${this.context}login`,
      });
    },
  },
});
